import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ReportPageComponent } from './report.component';
import { SaleReportPageComponent } from './sale-report/sale-report.component';

const routes: Routes = [
  {
    path: '',
    component: ReportPageComponent,
    children: [
      {
        path: '',
        component: SaleReportPageComponent,
      },
      {
        path: 'sale',
        component: SaleReportPageComponent,
      },
      {
        path: 'expenses',
        loadChildren: () => import('./expenses-report/expenses-report.module')
          .then(module => module.ExpensesReportPageModule)
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReportRoutingModule { }
